import './App.css';
import React, { useContext } from 'react';
import Listings from './Listings';
// import Navbar from './Navbar-Drawer';
import { Route, Routes, useNavigate, useParams } from 'react-router-dom';
import Show from './Show';
import Home from './Home';
import { RequireAuth, useAuthUser, useIsAuthenticated } from 'react-auth-kit'
import { Navigate } from "react-router-dom";
import ScrollToTop from './ScrollToTop';
import Page from './Page';
import ListingModForm from './ListingModForm';
import UserProfile from './UserProfile';
import Messages from './UserMessages';
import UserWrapper from './UserWrapper';
import LoginForm from './UserLoginForm';
import RegistrationForm from './UserRegistrationForm';
import ResetForm from './UserResetForm';
import ForgotForm from './UserForgotForm';
import listingService from './services/listing.service';
import UserVerifyCard from './UserVerifyCard';
import { ListingContext } from './contexts/ListingContext';
import userService from './services/user.service';
 
function App() {
  const isAuthenticated = useIsAuthenticated()
  const auth = useAuthUser()
  const navigate = useNavigate()

  const {setMessage, setMessageStatus} = useContext(ListingContext)

  // const EditRedirect = () => {
  //   const { id } = useParams();
  //   return <Navigate to={`/listings/${id}`} />
  // }

  const ListingsRedirect = () => {
    return <Navigate to={'/listings'} />
  }

  const VerifyLink = () => {
    const { token } = useParams()
    userService.verify(token)
      .then(({ data }) => {
        if (data.messageStatus === 'success') {
          setMessage(data.message)
          setMessageStatus(data.messageStatus)
          navigate('/login')
        } else {
          navigate('/')
        }
      })
  }

  const ResetLink = () => {
    const { token } = useParams()
    userService.setToken(token)
      .then(({ data }) => {
        window.localStorage.setItem('returned stuff', data)
        if (data.messageStatus === 'success') {
          console.log('here')
          setMessage(data.message)
          setMessageStatus(data.messageStatus)
          navigate('/reset')
        } else {
          navigate('/')
        }
      })
  }

  const EditRedirect = async () => {
    const { id } = useParams();
    const listing = await listingService.get(id)
    if (auth().id !== listing.author._id) {
      return <Navigate to={`/listings/${id}`} />
    }
  }

  return (
    <div className="App">
      <Page>
        <ScrollToTop />
        <Routes>
          <Route path='/' element={<Home />} />
          <Route path='/profile/:id'
            element={
              <RequireAuth loginPath={'/login'}>
                <UserProfile />
              </RequireAuth>
            } />
          <Route path='/messages' element={
                        <RequireAuth loginPath={'/login'}>
                        <Messages />
                        </RequireAuth>
} />
          <Route path='/listings/new'
            element={
              <RequireAuth loginPath={'/login'}>
                <ListingModForm />
              </RequireAuth>
            } />
          <Route path='/listings' element={<Listings />} />
          <Route path='/listings/:id/edit'
            element={isAuthenticated() ?
              <RequireAuth loginPath={'/login'}>
                <ListingModForm />
              </RequireAuth>
              :
              <EditRedirect />
            } />
          <Route path='/listings/:id' element={<Show />} />
          <Route path='/login' element={isAuthenticated() ? <ListingsRedirect /> : <UserWrapper form={<LoginForm />} /> } />
          <Route path='/verify' element={ isAuthenticated() ? <ListingsRedirect /> : <UserWrapper form={<UserVerifyCard />} />
          } />
          <Route path='/verify/:token' element={ isAuthenticated() ? <ListingsRedirect /> :<VerifyLink />
          } />
          <Route path='/register' element={isAuthenticated() ? <ListingsRedirect /> : <UserWrapper form={<RegistrationForm />} /> } />
          <Route path='/forgot' element={isAuthenticated() ? <ListingsRedirect /> : <UserWrapper form={<ForgotForm />} /> } />
          <Route path='/reset' element={isAuthenticated() ? <ListingsRedirect /> : <UserWrapper form={<ResetForm />} /> } />

          <Route path='/reset/:token' element={isAuthenticated() ? <ListingsRedirect /> : <UserWrapper form={<ResetLink />} /> } />
          <Route path='/*' element={<ListingsRedirect /> } />
        </Routes>
      </Page>
    </div>
  );
}

export default App;
